        </div>
        <div class="video__info">
          <h5 class="video__label">{{literatur.title}}</h5>
          <p class="video__descriptions video__descriptions--full">{{literatur.description}}</p>
        </div>
      </div>
    </div>
    <modal-component :show="show" position="center">
      <div class="modal__header">
        <h3>{{video.title}}</h3>
        <span v-on:click="showModal()"></span>
      </div>
      <div class="modal__content modal__video no--padding">
        <iframe :src="'https://www.youtube.com/embed/' + video.path" frameborder="0" allow="accelerometer; autoplay; encrypted-media; gyroscope; picture-in-picture" allowfullscreen></iframe>
      </div>
    </modal-component>
  </div>
</template>

<script>
  import ModalComponent from '@/helpers/ModalComponent.vue'
  import {Http} from '@/helpers/http-helper'

  export default {
    components: {ModalComponent},
    component: {
      ModalComponent
    },
    data () {
      return {
        showImg: 1,
        show: false,
        literature: {},
        video: {}
      }
    },
    mounted () {
      this.fetchAll()
    },
    methods: {
      showModal: function () {
        this.show = !this.show
      },
      fetchAll: function () {
        Http.get(`/literature/type/` + this.$route.params.type)
          .then(response => {
            this.literature = response.data
          })
      },
      fetchById: function (idVideo) {
        this.showModal()

        Http.get(`/literature/` + idVideo)
          .then(response => {
            this.video = response.data
          })
          .catch(e => {
            this.errors = e.body
          })
      },
    }
  }
</script>



